}
.advantages__item:nth-child(2) .advantages__text::after {
  content: "";
  position: absolute;
  left: -77px;
  top: 1px;
  width: 52px;
  background-image: url("../../img/advantage-powder@1x.png");
  background-size: contain;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .advantages__item:nth-child(2) .advantages__text::after {
    background-image: url("../../img/advantage-powder@2x.png");
  }
}
.advantages__item:nth-child(3) .advantages__text::after {
  content: "";
  position: absolute;
  left: -74px;
  top: 7px;
  background-image: url("../../img/advantage-dining@1x.png");
